.home {
  &__container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding: 20px;
    max-width: 1440px;
    margin: 0 auto;
  }

  &__card {
    background-color: #ffffff;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    text-align: center;
    padding: 15px;
    width: calc(25% - 20px);

    &-title {
      font-size: 1rem;
      color: #343a40;
      margin: 10px 0;
      text-transform: uppercase;
    }

    &-description {
      font-size: 1rem;
      color: #6c757d;
      padding: 0 10px 10px;
    }
  }

  @media (max-width: 1440px) {
    &__card {
      width: calc(25% - 20px);
    }
  }

  @media (max-width: 1200px) {
    &__card {
      width: calc(33.33% - 20px);
    }
  }

  @media (max-width: 900px) {
    &__card {
      width: calc(50% - 20px);
    }
  }

  @media (max-width: 600px) {
    &__card {
      width: calc(100% - 20px);
    }
  }
}
